<template>
   <div class="max-w-xl mx-auto px-2 p-2 rounded">
       <form @submit.prevent="dataSubmit" >
            <div class="w-full flex gap-8 bg-white mt-8 p-4 rounded-md">
                <div class=" flex flex-col justify-center mx-auto gap-8  mt-8 md:mt-0">
                    <h2 class="text-2xl text-brand">User Edit Page</h2>

                    <div class="block w-full relative">
                        <Input
                            type="text"
                            v-model="form.name"
                            class="w-full focus:outline-none border  text-center"
                            placeholder="Full Name"
                            autocomplete="off"
                            required />
                     </div>
                    <div class="block w-full relative">
                        <Input
                            type="number"
                            v-model="form.phone"
                            class="w-full focus:outline-none border text-center"
                            placeholder="Enter Your Phone Number"
                            autocomplete="off"
                            required />
                     </div>
                    <div class="block w-full relative">
                        <Select v-model="form.gender" class="w-full focus:outline-none border text-center" autocomplete="off" required>
                            <option value=""> Select Gender </option>
                            <option value="1">Male</option>
                            <option value="2">Female</option>
                        </Select>
                     </div>
                      <div class="block w-full">
                        <InputText
                            v-model="form.password" password-eye
                            type="password"
                            class="w-full focus:outline-none text-center"
                            placeholder="Set Password"
                            autocomplete="off"
                        />
                    </div>

                     <div class="flex justify-center">
                        <button type="submit" class="bg-brand text-white py-1.5 px-2 rounded-md flex items-center"> 
                            <spinner v-if="loading" />
                            Next
                        </button>
                     </div>
                </div>
            </div>
       </form>
   </div>
</template>

<script>
import Input from '../../../components/input-new.vue'
import Spinner from '../../../components/spinner.vue'
import Select from '../../../components/select.vue'
import InputText  from '../../../components/input-text.vue'
// import ShowError from "../../../components/show-error.vue";
export default {
   components:{
       Input,
       Spinner,
       Select,
       InputText
   },
   data() {
    return {
        form:{
            name: '',
            phone:  '',
            gender:'',
            password:'',
        },
        loading: false,
    }
  },
  created(){
        this.$store.dispatch('users/userEdit',{
            id: this.$route.params.id
        })
    },
   watch: {
        '$store.state.users.user_edit'( data ){
            console.log('ssd',data.data.password)
          this.form.name = data.data.name 
          this.form.phone = data.data.phone 
          this.form.gender = data.data.gender 
          this.form.password = data.data.password 
        }
    },
  computed:{
    list() {
        return this.$store.state.users.user_edit.data;
    },  
  },

   methods:{
       dataSubmit(){
        this.$store.dispatch("users/editDataSubmit", {
                update_id: this.$route.params.id,
                data:{
                    name: this.form.name,
                    phone: this.form.phone,
                    gender: this.form.gender,
                    password: this.form.password
                },
                
            }).then( () => {
                this.$toast.success(`User successfully Updated !!!`,{
                   position: "top",
                   duration:5000
                });
                this.$router.push({
                    path:'/admin/users'
                })
            })
       }
   }
}
</script>

<style>
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
</style>